// convert a 3-byte triplet into an rgb color
// e.g. rgbcolor("12, 34, 56") => rgb(12, 34, 56)
@function rgbcolor($triplet, $alpha: 1) {
    @return #{"rgba(" $triplet ", " $alpha ")"};
}

